"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";

export default function AdminPage() {
  const router = useRouter();

  useEffect(() => {
    // Check if user is logged in
    const isLoggedIn = localStorage.getItem("isLoggedIn");
    if (!isLoggedIn) {
      router.push("/login");
    }
  }, [router]);

  const handleLogout = () => {
    localStorage.removeItem("isLoggedIn");
    router.push("/login");
  };

  return (
    <div className="min-h-screen bg-black text-white p-8">
      <header className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold">Admin Dashboard - Sistem Apotek</h1>
        <Button onClick={handleLogout} variant="outline" className="text-white border-white">
          Logout
        </Button>
      </header>
      <main>
        <section className="bg-white text-black p-6 rounded-lg shadow-md">
          <h2 className="text-2xl font-semibold mb-4">Dashboard Overview</h2>
          <p>Welcome to the admin dashboard. Here you can manage the Sistem Apotek.</p>
          {/* Add more admin dashboard features here */}
        </section>
      </main>
    </div>
  );
}
